@media all and (max-width: 480px) {
  .hero {
    padding-top: 100px;
  }
}

.hero .header {
  margin-bottom: 50px;
}

.hero .header h1 {
  font-size: 64px;
  color: #eac440;
}

@media all and (max-width: 480px) {
  .hero .header h1 {
    font-size: 32px;
  }
}

.hero .header p {
  font-size: 32px;
  color: white;
}

@media all and (max-width: 480px) {
  .hero .header p {
    font-size: 24px;
  }
}

.hero .links {
  flex: 1 0 100%;
  display: flex;
  justify-content: center;
}

@media all and (max-width: 480px) {
  .hero .links {
    flex-direction: column;
    justify-content: flex-start;
  }
}

.hero .links a {
  height: 60px;
  padding: 10px 30px;
  font-size: 24px;
  color: white;
  transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.hero .links a:hover {
  color: #00000a;
  background: #eac440;
  cursor: pointer;
}

.hero .links :nth-child(1) {
  border-left: 2px solid #eac440;
}

.hero .links :nth-child(2) {
  border: 2px solid #eac440;
}

.hero .links :nth-child(3) {
  border-right: 2px solid #eac440;
}

.about {
  width: 100%;
  background: white;
  padding: 50px 0 100px 0;
}

.about .heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.about .heading h1 {
  text-align: center;
  margin-bottom: 10px;
  font-size: 64px;
  color: #00000a;
}

.about .heading .line {
  width: 100px;
  height: 5px;
  text-align: center;
  background: #00000a;
}

.about .description {
  max-width: 720px;
  margin: 50px auto;
  padding: 25px;
  background: #00000a;
  border-radius: 25px;
  text-align: center;
}

@media all and (max-width: 768px) {
  .about .description {
    max-width: 400px;
  }
}

.about .description p {
  color: white;
  font-size: 24px;
}

.about .description a {
  padding: 5px;
  border: 2px solid white;
  color: white;
}

.about .tech-stack {
  padding: 10px;
  background: #eac440;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12);
}

.about .tech-stack .heading {
  margin-bottom: 25px;
}

.about .tech-stack .heading h3 {
  padding-left: 5px;
  text-align: center;
  font-size: 32px;
  color: #00000a;
  border-left: 3px solid #00000a;
}

.about .tech-stack .items {
  text-align: center;
}

.about .tech-stack .items .item {
  font-size: 48px;
  color: #00000a;
  margin: 0 5px;
  transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.about .tech-stack .items .item:hover {
  color: white;
}

.projects {
  width: 100%;
  background: #00000a;
  padding: 50px 0 150px 0;
  position: relative;
}

.projects .heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.projects .heading h1 {
  text-align: center;
  margin-bottom: 10px;
  font-size: 64px;
  color: white;
}

.projects .heading .line {
  width: 100px;
  height: 5px;
  text-align: center;
  background: white;
}

.projects .project-cards-container {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.projects .project-cards-container .project-card {
  width: 250px;
  height: 250px;
  position: relative;
  display: flex;
  border: 2px solid white;
  margin: 10px;
}

.projects .project-cards-container .project-card .title {
  width: 100%;
  align-self: flex-end;
  text-align: center;
  font-size: 24px;
  background: white;
  color: #00000a;
  line-height: 32px;
}

.projects .project-cards-container .project-card .overlay {
  position: absolute;
  width: 100%;
  height: calc(100% - 32px);
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.25);
  display: flex;
  align-items: flex-end;
  opacity: 0;
  transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.projects .project-cards-container .project-card .overlay a {
  width: 50%;
  background: #eac440;
  padding: 5px;
  text-align: center;
  font-size: 16px;
}

.projects .project-cards-container .project-card .overlay a:hover {
  cursor: pointer;
  transition: 0.25s linear;
  background: black;
  color: white;
}

.projects .project-cards-container .project-card:hover > .overlay {
  opacity: 1;
}

.contact {
  width: 100%;
  background: white;
  padding: 50px 0 100px 0;
}

.contact .heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
}

.contact .heading h1 {
  text-align: center;
  margin-bottom: 10px;
  font-size: 64px;
  color: #00000a;
}

.contact .heading .line {
  width: 100px;
  height: 5px;
  text-align: center;
  background: #00000a;
}

.contact .links {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.contact .links .item {
  margin: 0 25px;
}

.contact .links .item:hover svg {
  color: #eac440;
  background: #00000a;
}

.contact .links .item svg {
  width: 150px;
  height: 150px;
  padding: 40px;
  text-align: center;
  line-height: 75px;
  background: #eac440;
  border-radius: 100%;
  transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.contact .links .item p {
  display: block;
  text-align: center;
}

.footer {
  width: 100%;
  background: #eac440;
  padding: 50px 0 100px 0;
}

.footer .text {
  text-align: center;
  font-size: 32px;
}
